<template>
  <div class="dashboard">
    <h1>📊 서버 상태 모니터링</h1>

    <div v-if="loading" class="loading">⏳ 데이터 로딩 중...</div>
    <div v-else-if="error" class="error">❌ {{ error }}</div>
    <div v-else class="status">
      <div class="status-item">
        <h2>서버 상태</h2>
        <p :class="statusClass">{{ healthData.status }}</p>
      </div>

      <div class="status-item">
        <h2>CPU 사용량</h2>
        <p>{{ healthData.cpu_usage }}</p>
      </div>

      <div class="status-item">
        <h2>메모리 사용량</h2>
        <p>{{ healthData.memory_usage }}</p>
      </div>

      <div class="status-item">
        <h2>Flask PID</h2>
        <p>{{ healthData.flask_pid }}</p>
      </div>

      <div class="status-item">
        <h2>Gunicorn 상태</h2>
        <p :class="gunicornClass">{{ healthData.gunicorn_status }}</p>
      </div>

      <div class="status-item">
        <h2>Gunicorn PID</h2>
        <p>{{ healthData.gunicorn_pid }}</p>
      </div>

      <button @click="fetchHealthData" class="refresh-btn">🔄 새로고침</button>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import axios from "axios";

export default {
  setup() {
    const healthData = ref({});
    const loading = ref(true);
    const error = ref(null);

    const fetchHealthData = async () => {
      try {
        const response = await axios.get("https://dodamee.cloud/health");
        healthData.value = response.data;
        error.value = null;
      } catch (err) {
        error.value = "서버에서 데이터를 불러올 수 없습니다.";
      } finally {
        loading.value = false;
      }
    };

    // 3초마다 데이터 자동 갱신
    onMounted(() => {
      fetchHealthData();
      setInterval(fetchHealthData, 3000);
    });

    // 상태에 따른 CSS 클래스 적용
    const statusClass = () => (healthData.value.status === "running" ? "status-ok" : "status-error");
    const gunicornClass = () => (healthData.value.gunicorn_status === "running" ? "status-ok" : "status-error");

    return { healthData, loading, error, fetchHealthData, statusClass, gunicornClass };
  },
};
</script>

<style>
.dashboard {
  font-family: Arial, sans-serif;
  max-width: 500px;
  margin: auto;
  text-align: center;
  padding: 20px;
  border: 2px solid #ddd;
  border-radius: 10px;
  background: #f9f9f9;
}

h1 {
  color: #333;
}

.loading {
  color: #666;
  font-size: 1.2rem;
}

.error {
  color: red;
  font-size: 1.2rem;
}

.status {
  margin-top: 20px;
}

.status-item {
  background: white;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.status-ok {
  color: green;
  font-weight: bold;
}

.status-error {
  color: red;
  font-weight: bold;
}

.refresh-btn {
  margin-top: 10px;
  padding: 10px;
  font-size: 1rem;
  cursor: pointer;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
}

.refresh-btn:hover {
  background: #0056b3;
}
</style>
