<template>
  <div>
    <h1>주식 매수 등록</h1>
    <form @submit.prevent="buyStock">
      <div>
        <label>종목:</label>
        <select v-model="stock_symbol">
          <option value="A">A 종목</option>
          <option value="B">B 종목</option>
        </select>
      </div>
      <div>
        <label>날짜:</label>
        <input type="date" v-model="trade_date" />
      </div>
      <div>
        <label>매수 가격:</label>
        <input type="number" v-model="price" />
      </div>
      <div>
        <label>수량:</label>
        <input type="number" v-model="quantity" />
      </div>
      <div>
        <label>총 가격:</label>
        <input type="number" v-model="total_amount" />
      </div>
      <button type="submit">매수 등록</button>
    </form>
  </div>
</template>

<script>
import Cookies from 'js-cookie';

export default {
  data() {
    return {
      stock_symbol: '',
      trade_date: '',
      price: 0,
      quantity: 0,
      total_amount: 0,
      transactions: [] // 쿠키에 저장할 매수 내역
    };
  },
  methods: {
    buyStock() {
      // 새로운 매수 정보 추가
      const newTransaction = {
        stock_symbol: this.stock_symbol,
        trade_date: this.trade_date,
        price: this.price,
        quantity: this.quantity,
        total_amount: this.total_amount
      };

      // 기존 쿠키에 저장된 데이터 가져오기
      const savedTransactions = Cookies.get('transactions');
      this.transactions = savedTransactions ? JSON.parse(savedTransactions) : [];

      // 새로운 데이터 추가 후 쿠키에 저장
      this.transactions.push(newTransaction);
      Cookies.set('transactions', JSON.stringify(this.transactions), { expires: 36500 }); // 유효기간 100년 설정

      alert('주식 매수가 성공적으로 저장되었습니다.');
    }
  }
};
</script>
