<template>
  <div>
    <h1>주식 판매 완료</h1>
    <table>
      <thead>
        <tr>
          <th>종목</th>
          <th>매수 날짜</th>
          <th>매수 가격</th>
          <th>판매 날짜</th>
          <th>판매 가격</th>
          <th>수익금</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(stock, index) in soldTransactions" :key="index">
          <td>{{ stock.stock_symbol }}</td>
          <td>{{ stock.trade_date }}</td>
          <td>{{ stock.price }}원</td>
          <td>{{ stock.sell_date }}</td>
          <td>{{ stock.sell_price }}원</td>
          <td :style="profitStyle(stock)">{{ calculateProfit(stock) }}원</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Cookies from 'js-cookie';

export default {
  data() {
    return {
      transactions: [] // 쿠키에서 불러온 모든 매수 내역
    };
  },
  computed: {
    soldTransactions() {
      // 판매된 주식만 필터링
      return this.transactions.filter(transaction => transaction.sell_price);
    }
  },
  created() {
    // 쿠키에서 매수 내역을 불러와서 저장
    const savedTransactions = Cookies.get('transactions');
    this.transactions = savedTransactions ? JSON.parse(savedTransactions) : [];
  },
  methods: {
    calculateProfit(stock) {
      // 수익금 계산 (판매 가격 - 매수 가격) * 수량
      const profit = (stock.sell_price - stock.price) * stock.quantity;
      return profit.toLocaleString(); // 수익금을 표시할 때 천단위 콤마 추가
    },
    profitStyle(stock) {
      // 수익금에 따른 색상 설정 (0보다 크면 빨간색, 0보다 작으면 파란색)
      const profit = (stock.sell_price - stock.price) * stock.quantity;
      return {
        color: profit > 0 ? 'red' : profit < 0 ? 'blue' : 'black',
      };
    }
  }
};
</script>
