<template>
  <div>
    <h1>한국 주식 가격 조회 (Yahoo Finance)</h1>
    <input v-model="stockSymbol" placeholder="종목 코드를 입력하세요" />
    <button @click="fetchStockPrice">조회</button>
    <p v-if="stockPrice !== null">{{ stockSymbol }} 현재 가격: {{ stockPrice }}원</p>
    <p v-if="errorMessage">{{ errorMessage }}</p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      stockSymbol: "", // 사용자가 입력할 주식 코드
      stockPrice: null,
      errorMessage: "",
    };
  },
  methods: {
    async fetchStockPrice() {
      if (!this.stockSymbol) {
        this.errorMessage = "종목 코드를 입력하세요.";
        return;
      }

      try {
        // Yahoo Finance API 호출
        const response = await axios.get(
          `https://query1.finance.yahoo.com/v7/finance/quote?symbols=${this.stockSymbol}.KS`
        );
        const stockData = response.data.quoteResponse.result[0];

        if (stockData) {
          this.stockPrice = stockData.regularMarketPrice;
          this.errorMessage = "";
        } else {
          this.errorMessage = "해당 종목의 데이터를 찾을 수 없습니다.";
        }
      } catch (error) {
        this.errorMessage = "주식 데이터를 가져오는 데 실패했습니다.";
        console.error(error);
      }
    },
  },
};
</script>

<style>
input {
  padding: 10px;
  margin-right: 10px;
}
button {
  padding: 10px;
}
p {
  margin-top: 20px;
}
</style>
