<template>
  <div>
    <h1>주식 매수 조회</h1>
    <table>
      <thead>
        <tr>
          <th>종목</th>
          <th>날짜</th>
          <th>매수 가격</th>
          <th>수량</th>
          <th>총 가격</th>
          <th>현재 가격</th>
          <th>차이</th>
          <th>판매</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(stock, index) in unsoldTransactions" :key="index">
          <td>{{ stock.stock_symbol }}</td>
          <td>{{ stock.trade_date }}</td>
          <td>{{ stock.price }}원</td>
          <td>{{ stock.quantity }}</td>
          <td>{{ stock.total_amount }}원</td>
          <td>{{ stock.current_price !== null ? stock.current_price + '원' : '불러오는 중...' }}</td>
          <td :style="getDifferenceStyle(stock)">
            {{ calculateDifference(stock) }}원
          </td>
          <td><button @click="sellStock(index)">판매</button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import axios from 'axios';

export default {
  data() {
    return {
      transactions: [] // 쿠키에서 불러온 모든 매수 내역
    };
  },
  computed: {
    unsoldTransactions() {
      // 판매되지 않은 주식만 필터링
      return this.transactions.filter(transaction => !transaction.sell_price);
    }
  },
  created() {
    // 쿠키에서 매수 내역을 불러와서 저장
    const savedTransactions = Cookies.get('transactions');
    this.transactions = savedTransactions ? JSON.parse(savedTransactions) : [];

    // 현재 가격을 API로 불러옴
    this.fetchCurrentPrices();
  },
  methods: {
    fetchCurrentPrices() {
      this.transactions.forEach((stock) => {
        // WTI 가격 API 호출
        axios.post('http://152.69.226.208:5500/wtiEtn')
          .then(response => {
            // API에서 현재 가격을 받아서 저장
            stock.current_price = response.data.current_price; // this.$set 대신 직접 할당
          })
          .catch(error => {
            console.error('가격을 불러오는데 실패했습니다.', error);
            stock.current_price = null; // 실패 시 null로 설정
          });
      });
    },
    calculateDifference(stock) {
      if (stock.current_price !== null) {
        return (stock.current_price - stock.price).toLocaleString(); // 차이 계산
      }
      return 'N/A';
    },
    getDifferenceStyle(stock) {
      const difference = stock.current_price - stock.price;
      if (difference > 0) {
        return { color: 'red' }; // 차이가 0보다 크면 빨간색
      } else if (difference < 0) {
        return { color: 'blue' }; // 차이가 0보다 작으면 파란색
      }
      return {};
    },
    sellStock(index) {
      const sell_price = prompt('판매 가격을 입력하세요');
      const sell_date = prompt('판매 날짜를 입력하세요 (YYYY-MM-DD)');

      // 매도 정보 업데이트
      this.transactions[index].sell_price = sell_price;
      this.transactions[index].sell_date = sell_date;

      // 쿠키에 다시 저장
      Cookies.set('transactions', JSON.stringify(this.transactions), { expires: 36500 });

      alert('주식이 성공적으로 판매되었습니다.');
    }
  }
};
</script>
